import Endpoint from '@/services/Endpoint'

export default {
  transferTypes (payload) {
    return Endpoint().get('/s1/connect/ft/transferType', {
      params: payload
    })
  },
  transferTypeTree (payload) {
    return Endpoint().get('/s1/connect/ft/transferType/tree', {
      params: payload
    })
  },
  transferPurposes (payload) {
    return Endpoint().get('/s1/connect/ft/transferPurpose', {
      params: payload
    })
  },
  calculateValueDate (payload) {
    return Endpoint().post('/s1/connect/ft/single/valueDate', payload)
  },
  createSingleFundsTransfer (payload) {
    return Endpoint().post('/s1/connect/ft/single', payload)
  },
  createBulkFundsTransfer (payload) {
    return Endpoint().post('/s1/connect/ft/bulk/create', payload.formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  proposeBulkFundsTransfer (payload) {
    return Endpoint().post('/s1/connect/ft/bulk/propose', payload)
  },
  refreshBulkFundsTransfer (payload) {
    return Endpoint().post('/s1/connect/ft/bulk/refresh', payload)
  },
  currencySummaries (payload) {
    return Endpoint().get(`/s1/connect/ft/${payload.transferId}/currencySummary`, {
      params: payload
    })
  },
  fundsTransferWorkflowInstances (payload) {
    return Endpoint().get('/s1/connect/ft/workflow/instance', {
      params: payload
    })
  },
  fundsTransferChildren (payload) {
    return Endpoint().get(`/s1/connect/ft/${payload.transferId}/transfer`, {
      params: payload
    })
  }
}
