<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <vs-table class="mb-base" :data="workflowInstanceTableData" :noDataText="workflowInstanceTableNoDataText" :sst="true" search @search="filterWorkflowInstanceTable" @sort="orderWorkflowInstanceTable">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3" @click="refreshWorkflowInstanceTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="workflowInstanceTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ workflowInstanceTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="workflowInstanceTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="instanceId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="beneficiaryName">
            {{ this.$t('BENEFICIARY') }}
          </vs-th>
          <vs-th sort-key="transferTypeName">
            {{ this.$t('TRANSFER_TYPE') }}
          </vs-th>
          <vs-th sort-key="amountDebit">
            {{ this.$t('DEBIT_AMOUNT') }}
          </vs-th>
          <vs-th sort-key="creationDate">
            {{ this.$t('CREATION_DATE') }}
          </vs-th>
          <vs-th sort-key="transferStatusDescription">
            {{ this.$t('STATUS') }}
          </vs-th>
          <vs-th sort-key="approvedCount">
            {{ this.$t('TASKS') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].instanceId">
              <router-link :to="{ name: 'ViewFundsTransferWorkflowInstance', params: { id: data[index].instanceId } }">{{ data[index].instanceId }}</router-link>
            </vs-td>
            <vs-td :data="data[index].beneficiaryName">
              {{ beneficiaryName(data[index]) | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].transferTypeName">
              {{ data[index].transferTypeName | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].amountDebit">
              {{ data[index].amountDebit | currency('') | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].creationDate">
              {{ data[index].creationDate | formatUnixDate }}
            </vs-td>
            <vs-td :data="data[index].transferStatusDescription">
              {{ data[index].transferStatusDescription | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].approvedCount">
              <span class="mr-2"><i class="far fa-check-circle text-success"></i> {{ data[index].approvedCount | placeholder('0') }}</span>
              <span class="mr-2"><i class="far fa-times-circle text-danger"></i> {{ data[index].rejectedCount | placeholder('0') }}</span>
              <span class="mr-2"><i class="far fa-question-circle text-warning"></i> {{ data[index].pendingCount | placeholder('0') }}</span>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="workflowInstanceTableTotalPages" v-model="workflowInstanceTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import FundsTransferWorkflowInstanceTableMixin from '@/mixins/ft/FundsTransferWorkflowInstanceTableMixin'

export default {
  name: 'funds-transfer-workflow-instance-browse',
  mixins: [AlertMixin, FundsTransferWorkflowInstanceTableMixin],
  methods: {
    beneficiaryName (item) {
      if (item.typeBehaviourEnumId.startsWith('FT_TYPE_BULK')) {
        return `${item.paymentFileName} (${item.transactionCount})`
      } else if (item.typeBehaviourEnumId === 'FT_TYPE_SINGLE_OWN_ACCT') {
        return item.creditAccountNumber
      } else {
        return item.beneficiaryName
      }
    }
  },
  mounted: function () {
    this.loadWorkflowInstanceTableData()
  }
}
</script>
