import FundsTransferService from '@/services/ft/FundsTransferService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let FundsTransferWorkflowInstanceTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultWorkflowInstanceTableFilter: {
      type: String,
      default: null
    },
    defaultWorkflowInstanceTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      workflowInstanceTableData: [],
      workflowInstanceTableFilterTimer: null,
      workflowInstanceTableFilter: this.defaultWorkflowInstanceTableFilter,
      workflowInstanceTableOrderBy: this.defaultWorkflowInstanceTableOrderBy,
      workflowInstanceTableContainer: 'loading-container',
      workflowInstanceTableBusy: false,
      workflowInstanceTableTotalRows: 0,
      workflowInstanceTableCurrentPage: 1,
      workflowInstanceTablePerPage: 10
    }
  },
  computed: {
    workflowInstanceTableNoDataText: function () {
      return this.workflowInstanceTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    workflowInstanceTableTotalPages: function () {
      return Math.ceil(this.workflowInstanceTableTotalRows / this.workflowInstanceTablePerPage)
    },
    workflowInstanceTablePagingInfo: function () {
      return this.pagingInfo(this.workflowInstanceTableCurrentPage, this.workflowInstanceTablePerPage, this.workflowInstanceTableTotalRows)
    }
  },
  methods: {
    loadWorkflowInstanceTableData (loadingContainerId) {
      let self = this
      this.workflowInstanceTableBusy = true
      this.workflowInstanceTableContainer = loadingContainerId || this.workflowInstanceTableContainer
      this.startLoading(this.workflowInstanceTableContainer)
      FundsTransferService.fundsTransferWorkflowInstances({
        pageIndex: this.workflowInstanceTableCurrentPage - 1,
        pageSize: this.workflowInstanceTablePerPage,
        filter: this.workflowInstanceTableFilter,
        orderByField: this.workflowInstanceTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.workflowInstanceTableContainer)
        self.workflowInstanceTableBusy = false
        self.workflowInstanceTableTotalRows = response.data.totalRows
        self.workflowInstanceTableData = response.data.instanceList || []
      }).catch(function (error) {
        self.stopLoading(self.workflowInstanceTableContainer)
        self.workflowInstanceTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshWorkflowInstanceTable () {
      this.loadWorkflowInstanceTableData()
    },
    filterWorkflowInstanceTable (filter) {
      let self = this
      if (this.workflowInstanceTableFilter !== filter) {
        clearTimeout(this.workflowInstanceTableFilterTimer)
        this.workflowInstanceTableFilterTimer = setTimeout(() => {
          self.workflowInstanceTableFilter = filter
          self.loadWorkflowInstanceTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.workflowInstanceTableFilterTimer)
      }
    },
    orderWorkflowInstanceTable (orderBy, active) {
      this.workflowInstanceTableOrderBy = (active ? '' : '-') + orderBy
      this.loadWorkflowInstanceTableData()
    }
  },
  watch: {
    workflowInstanceTableCurrentPage: function () {
      this.refreshWorkflowInstanceTable()
    },
    workflowInstanceTablePerPage: function () {
      this.refreshWorkflowInstanceTable()
    }
  }
}

export default FundsTransferWorkflowInstanceTableMixin
